import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'

export default function CountaryMeal() {
    let [areaMeal,setAreaMeal]=useState([])
    let [isloading,setIsLoading]=useState(true)
    let {countary}=useParams()

    
    let navg =useNavigate()
    console.log(countary)
    async function getAllAreaMeal() {
        setIsLoading(true)
        let req=await fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?a=${countary}`).catch((err)=>{
            console.log(err)
            setIsLoading(false)
        })
        let data=await req.json()
        if(data?.meals){
            setAreaMeal(data.meals)
            setIsLoading(false)
        }

    }
    function navigat(id){
        navg('/ingredientDeteiles/'+id)
    }

    useEffect(()=>{
        getAllAreaMeal()
    },[])

    return <>
    {isloading? <Spinner/>:
    <div className="category">
    <div className="row g-4">
        {areaMeal?.map((element)=>{
            return <div className="itemCategory col-md-3 col-sm-4 cursor position-relative  rounded " key={element.idMeal} onClick={()=>{navigat(element.idMeal)}}>
                        <img src={element.strMealThumb} alt={element.strMeal}  className='w-100'/>
                        <div className='detelies p-2 text-center position-absolute d-flex justify-content-center align-items-center flex-column'>
                            <h4>{element.strMeal}</h4>
                        </div>
                    </div>
        })}
        
    </div>
    </div>}
    
    
    
    </>
}
