import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useNavigate } from 'react-router-dom'
import Aos from 'aos'
import 'aos/dist/aos.css'
export default function Area() {
  let [areaList,setAreaList]=useState([])
  let [isloading,setIsLoading]=useState(true)
  let navg=useNavigate()
  async function getAllArea() {
    setIsLoading(true)
    let req=await fetch('https://www.themealdb.com/api/json/v1/1/list.php?a=list').catch((err)=>{
      console.log(err)
      setIsLoading(false)
    })
    let data=await req.json()
    if(data?.meals){
      setAreaList(data.meals)
      setIsLoading(false)
    }

  }

  function sendCountary(countary){
    navg(`/countaryMeal/${countary}`)
  }
  

  useEffect(()=>{
    getAllArea()
    Aos.init()

  },[])


  return <>
  {isloading?<Spinner/>:
    <div className="row p-0">
      {areaList.map((element)=>{
        return <div className="col-md-2 col-sm-3 text-white cursor mb-5" data-aos="flip-left" onClick={()=>{sendCountary(element.strArea)}}>
                  <div className="countary text-center">
                    <i className="fa-solid fa-location-dot fa-2x mb-2 text-danger"></i>
                    <h4>{element.strArea}</h4>
                  </div>
                </div>
      })}
    </div>
  }
  </>
}
