
import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useNavigate } from 'react-router-dom'
import Aos from 'aos'
import 'aos/dist/aos.css'
export default function Categories() {
    let [categoryList,setcategoryList]=useState([])
    let [isLoading,setisLoading]=useState(true)
    let navg=useNavigate()

    async function getAllCategory(){
        setisLoading(true)
        let req=await fetch('https://www.themealdb.com/api/json/v1/1/categories.php').catch((err)=>{
            console.log(err)
            setisLoading(false)
        })
        let data=await req.json()
        if(data.categories)
        {
            setisLoading(false)
            setcategoryList(data.categories)    
        }
    }
    function navigat(name){
        navg('/mainIngredient/'+name)
    }
    useEffect(()=>{
        getAllCategory()
        Aos.init()
    },[])
    return <>
    {isLoading? <Spinner/>:
    <div className="category" >
    <div className="row g-5">
        {categoryList?.map((element)=>{
            return <div className="itemCategory col-md-3 col-sm-4  cursor position-relative p-5 rounded "   data-aos="flip-left"  key={element.idCategory} onClick={()=>{navigat(element.strCategory)}}>
                        <img src={element.strCategoryThumb} alt={element.strCategory}  className='w-100'/>
                        <div className='detelies p-2 text-center position-absolute d-flex justify-content-center align-items-center flex-column'>
                            <h4>{element.strCategory}</h4>
                            <p className='text-secondary-subtle'>{element.strCategoryDescription.split(' ').slice(0,15).join(" ")}</p>
                        </div>
                    </div>
        })}
        
    </div>
    </div>}
        
    </>
}
