import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'

export default function FilterIngredients() {
    let [isLoading,setisLoading]=useState(true)
    let [IngredientItemsList,setIngredientItems]=useState([])
    let {name}=useParams()
    let navg=useNavigate()




    async function getIngredientItems(){
        setisLoading(true)
        let req=await fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?i=${name}`).catch((err)=>{
            console.log(err)
            setisLoading(false)
        })
        let data=await req.json()
        if(data.meals)
        {
            setisLoading(false)
            setIngredientItems(data.meals)
        }
    }
    function navigat(id){
        navg('/ingredientDeteiles/'+id)
    }
    useEffect(()=>{
        getIngredientItems() 

    },[])


    return <>
    {isLoading? <Spinner/>:
    <div className="category">
    <div className="row py-5">
        {IngredientItemsList?.map((element)=>{
            return <div className="itemCategory col-md-3 mb-5  cursor position-relative  rounded " key={element.idMeal} onClick={()=>{navigat(element.idMeal)}}>
                        <img src={element.strMealThumb} alt={element.strCategory}  className='w-100 rounded'/>
                        <div className='detelies p-2 text-center position-absolute d-flex justify-content-center align-items-center flex-column'>
                            <h4>{element.strMeal}</h4>
                            {/* <p className='text-secondary-subtle'>{element.strCategoryDescription.split(' ').slice(0,15).join(" ")}</p> */}
                        </div>
                    </div>
        })}
        
    </div>
    </div>}
    
    </>
}
