import React from 'react'

export default function ContactUs() {
  function testInput(e){
    let reg=/^[A-Za-z_/\s]{3,}$/
    let input=document.querySelector('#massage').value
    if(reg.test(input)){
      document.querySelector('form').setAttribute('action','mailto:dm0189187@gmail.com')
      document.querySelector('.alert').classList.add("d-none")
    }else{
      e.preventDefault();
      document.querySelector('.alert').classList.remove("d-none")
    }
  }
  return <>
  <form >
    <textarea id='massage' name="massage"  placeholder='Enter Your Massage.....' rows={5}  className='form-control text-danger bg-black border-danger'></textarea>
    <div className='alert alert-danger mt-2 d-none'>Enter Valid Message</div>
    <button type="submit" className='btn btn-danger d-block ms-auto mt-3' onClick={testInput}>Send</button>
  </form>
  </>
}
