import React, { useEffect } from 'react'
import LogoImage from '../../assets/images/logo.png'
import $ from 'jquery'
import {NavLink } from 'react-router-dom'
export default function Navbar() {
    function navSlide(){
        let widthInner=$('.slide').outerWidth(true);
        $(".nav").css({left:-widthInner})
        $('.slide ul').css({left:-widthInner})
        $(".nav .close").on("click",function(e){
            let left=parseInt($(".nav").css("left"))
            if(left===0){
                $(".nav").animate({left:-widthInner},500)
                $('.close').addClass('fa-bars')
                $('.close').removeClass('fa-xmark')
                $('.slide ul').animate({left:-widthInner},500)

            }else{
                $(".nav").animate({left:"0"},500)
                $('.close').removeClass('fa-bars')
                $('.close').addClass('fa-xmark')
                $('.slide ul').animate({left:0},500)

            }
        })
    }
    useEffect(()=>{
        navSlide()

    },[])

    return <>
    
    <div className="nav z-index position-fixed">
    <div className='slide bg-black py-3 ps-4 pe-5  d-flex flex-column justify-content-between '>
        <ul className='position-relative'>
            <li  className='mb-3'>
                <NavLink  className={({ isActive}) => isActive ? "text-danger" : "text-white"}  to='search'>Search</NavLink>
            </li>
            <li className='mb-3'>
                <NavLink   className={({ isActive}) => isActive ? "text-danger" : "text-white"} to=''>Categories</NavLink>
            </li>
            <li className='mb-3'>
                <NavLink  className={({ isActive}) => isActive ? "text-danger" : "text-white"}  to='area'>Area</NavLink>
            </li>
            <li className='mb-3'>
                <NavLink   className={({ isActive}) => isActive ? "text-danger" : "text-white"} to='ingredients'>Ingredients</NavLink>
            </li>
            <li className='mb-3'>
                <NavLink  className={({ isActive}) => isActive ? "text-danger" : "text-white"}  to='contactUs'>Contact Us</NavLink>
            </li>
        </ul>
        <div className='text-white'>
            <a href="https://www.facebook.com/profile.php?id=100038744180049"><i className='fa-brands fa-facebook fa-xl me-2 text-danger '></i></a>    
            <a href="https://github.com/DoaaMohamed1310"><i className='fa-brands fa-github  fa-xl text-danger '></i></a>
            
            <span className='d-block mt-2'>
                Copyright  2019 All Rights<br/> Reserved.
            </span>

        </div>
        </div>
        <div className='slideNav p-2 bg-white d-flex justify-content-between flex-column align-items-center '>
            <img src={LogoImage} alt='Logo' className='w-100'/>
            <i className="fa-solid close fa-bars cursor fa-2x text-danger"></i>
            <i className="fa-solid fa-globe fa-2x text-danger"></i>
        </div>
        

    </div>
    </>
}
