import React from 'react'
import { createHashRouter, RouterProvider } from 'react-router-dom'
import Layout from './Component/Layout/Layout'
import Categories from './Component/Categories/Categories'
import NotFound from './Component/Notfound/NotFound'
import Search from './Component/Search/Search'
import Area from './Component/Area/Area'
import Ingredients from './Component/Ingredients/Ingredients'
import ContactUs from './Component/ContactUs/ContactUs'
import Mainingredient from './Component/Mainingredient/Mainingredient'
import IngredientDeteiles from './Component/IngredientDeteiles/IngredientDeteiles'
import CountaryMeal from './Component/CountaryMeal/CountaryMeal'
import FilterIngredients from './Component/FilterIngredients/FilterIngredients'

export default function App() {

  let routes=createHashRouter([
    {path:'',element:<Layout/>,children:[
      {index:true,element:<Categories/>},
      {path:'search',element:<Search/>},
      {path:'area',element:<Area/>},
      {path:'ingredients',element:<Ingredients/>},
      {path:'contactUs',element:<ContactUs/>},
      {path:'mainIngredient/:name',element:<Mainingredient/>},
      {path:'ingredientDeteiles/:id',element:<IngredientDeteiles/>},
      {path:'filterIngredients/:name',element:<FilterIngredients/>},

      {path:'countaryMeal/:countary',element:<CountaryMeal/>},


      {path:'*',element:<NotFound/>}
    ]}
  ])

  return <>
  <RouterProvider router={routes} />
  
  </>
}
