import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'

export default function Mainingredient() {
    let [isLoading,setisLoading]=useState(true)
    let [mainIngredientList,setMainIngredient]=useState([])
    let {name}=useParams()
    let navg=useNavigate()

    async function getAllMainIngredient(){
        
        setisLoading(true)
        let req=await fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?c=${name}`).catch((err)=>{
            console.log(err)
            setisLoading(false)
        })
        let data=await req.json()
        if(data.meals)
        {
            setisLoading(false)
            setMainIngredient(data.meals)    
        }
    }
    useEffect(()=>{
        getAllMainIngredient()
    },[])

    function navigat(id){
        navg('/ingredientDeteiles/'+id)
    }
    return <>


    {isLoading? <Spinner/>:
    <div className="category">
    <div className="row g-3">
        {mainIngredientList?.map((element)=>{
            return <div className="itemCategory col-md-3 col-sm-4   cursor position-relative  rounded " key={element.idMeal} onClick={()=>{navigat(element.idMeal)}} >
                        <img src={element.strMealThumb} alt={element.strCategory}  className='w-100 rounded'/>
                        <div className='detelies  text-center position-absolute d-flex justify-content-center align-items-center flex-column'>
                            <h6>{element.strMeal}</h6>
                        </div>
                    </div>
        })}
        
    </div>
    </div>}
    </>
}
