import Aos from 'aos'
import 'aos/dist/aos.css'
import $ from 'jquery'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'


export default function Search() {
  let [listSearch,setListSearch]=useState([])

  let navg =useNavigate()

  async function searchByMeal(value) {

    let req =await fetch(`https://www.themealdb.com/api/json/v1/1/search.php?s=${value}`).catch((err)=>{
      console.log(err)
    })
    let data=await req.json()
    if(data.meals){
      setListSearch(data.meals)
      $('.category').removeClass('d-none')
    }
    console.log(data)
  }
  function click(){
    let input=document.querySelector('.searchInput').value
    let req=/^\w{3,}$/
    if(req.test(input)){
      searchByMeal(input)
    }else{
      $('.alert').removeClass('d-none')
    }
    Aos.init()
  }

  function navigat(id){
    navg('/ingredientDeteiles/'+id)
}
  return <>
      <div className='search rounded d-flex justify-content-between align-items-center ps-2'>
        <input type="text" name="meal" id="meal" placeholder='Search By Meal Name' className='searchInput text-danger w-75 bg-black  rounded' />
        <button onClick={click} type='submit' className='btn btn-danger searchbtn'><i className='fa-solid fa-search'></i></button>
      </div>
        <div className='alert alert-danger mt-2 d-none'>Enter Valid Meal</div>
        
        <div className="category d-none py-5">
        <div className="row">
          {listSearch?.map((element)=>{
              return <div className="itemCategory col-md-3 cursor position-relative rounded mb-5 " data-aos="flip-left"  key={element.idMeal} onClick={()=>{navigat(element.idMeal)}}>
                          <img src={element.strMealThumb} alt={element.strMeal}  className='w-100 rounded'/>
                          <div className='detelies p-2 text-center position-absolute d-flex justify-content-center align-items-center flex-column'>
                              <h4>{element.strMeal}</h4>
                              <p className='text-secondary-subtle'>{element.strInstructions.split(' ').slice(0,15).join(" ")}</p>
                          </div>
                      </div>
          })}
            
        </div>
        </div>  </>
}
