import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useNavigate } from 'react-router-dom'
import Aos from 'aos'
import 'aos/dist/aos.css'
export default function Ingredients() {
  let [isLoading,setisLoading]=useState(true)
  let [IngredientList,setIngredient]=useState([])
  let navg=useNavigate()




  async function getIngredient(){
      setisLoading(true)
      let req=await fetch(`https://www.themealdb.com/api/json/v1/1/list.php?i=list`).catch((err)=>{
          console.log(err)
          setisLoading(false)
      })
      let data=await req.json()
      if(data.meals)
      {
          setisLoading(false)
          setIngredient(data.meals)
      }
  }

function navigat(name){
  navg(`/filterIngredients/${name}`)
}
  useEffect(()=>{
    getIngredient() 
    Aos.init()

},[])
  return <>
  {isLoading?<Spinner/>:
    <div className="row">
      {IngredientList?.slice(0,25).map((element)=>{
        return <div className="col-md-4  text-white mb-5 cursor" data-aos="flip-left"  key={element.idIngredient} onClick={()=>{navigat(element.strIngredient)}}>
                  <div className="countary text-center">
                  <i className="fa-solid fa-utensils fa-2x text-danger"></i>
                    <h4>{element.strIngredient}</h4>
                    <p>{element?.strDescription?.split(' ').slice(0,15).join(" ")}</p>
                  </div>
                </div>
      })}
    </div>
  }
  
  
  </>
}
