import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner'

export default function IngredientDeteiles() {
    let [isLoading,setisLoading]=useState(true)
    let [IngredientList,setIngredient]=useState([])
    let {id}=useParams()



    async function getIngredientDeteiles(){
        setisLoading(true)
        let req=await fetch(`https://www.themealdb.com/api/json/v1/1/lookup.php?i=${id}`).catch((err)=>{
            console.log(err)
            setisLoading(false)
        })
        let data=await req.json()
        if(data.meals)
        {
            setisLoading(false)
            setIngredient(data.meals)

        }
    }


    function loop(){
        let recipe=''
        for (let i = 1; i < 20; i++) {
                if(IngredientList[0] ){
                    if(IngredientList[0][`strMeasure${i}`]!==null && IngredientList[0][`strMeasure${i}`]!=='' && IngredientList[0][`strIngredient${i}`]!==null && IngredientList[0][`strIngredient${i}`]!=='' ){
                        recipe+=`
                            <li class='alert alert-info m-2 p-1 ' >
                                ${IngredientList[0][`strMeasure${i}`]+" "+IngredientList[0][`strIngredient${i}`]}
                            </li>
                        `
                    }
                }
            }
            let x=document.querySelector('.recipe')
            if(x){
                x.innerHTML=recipe
        }
    }
    
    useEffect(()=>{
        getIngredientDeteiles() 
        loop()
    },[])
    useEffect(()=>{
        loop()
    },[IngredientList])

    return <>
    {isLoading?<Spinner/>:
    <div className=' d-flex justify-content-center align-items-center py-5 text-white'>
            <div className="row">
                <div className="col-md-4 position-relative">
                    <img src={IngredientList[0]?.strMealThumb} alt="" className='w-100 rounded'/>
                </div>
                <div className="col-md-8">
                    <h2 className='text-danger'>{IngredientList[0]?.strMeal}</h2>
                    <p className='text-white'>{IngredientList[0]?.strInstructions}</p>
                    <h4>Area : {IngredientList[0]?.strArea}</h4>
                    <h4>Category :{IngredientList[0]?.strCategory}</h4>
                    <div>
                        <h4 className='mb-3'>Recipes :</h4>
                        <ul className="recipe">
                        </ul>
                    </div>
                    <div className='mb-4'>
                        <h4 className='mb-3'>Tags :</h4>
                        {IngredientList[0]?.strTags?.split(',').map((ele)=>{
                            return<span className='alert alert-info p-2 me-2 ms-2'>{ele}</span>
                        })}

                    </div>
                    <div className='mt-3'>
                        <a href={IngredientList[0].strSource} className='btn btn-success text-white me-3'>Source</a>
                        <a href={IngredientList[0].strYoutube} className='btn btn-danger text-white'>Youtube</a>
                    </div>
                </div>
            </div>
    </div>
    
    }
    
    
    </>
}
